<template>
  <div class="search-box">
    <i class="icon-search"></i>
    <input ref="query" v-model="query" class="box" :placeholder="placeholder"/>
    <i @click="clear" v-show="query" class="icon-dismiss"></i>
  </div>
</template>

<script type="text/ecmascript-6">
  

  export default {
    props: {
      placeholder: {
        type: String,
        default: '搜索歌曲、歌手'
      },
    },
    data() {
      return {
        query: ''
      }
    },
    methods: {
      clear() {
        this.query = ''
      },
      setQuery(query) {
        this.query = query
      },
      blur() {
        this.$refs.query.blur()
      },
      blur(){
        this.$refs.query.blur()
      }
    },
    created(){
      this.$watch('query',(newQuery)=>{
        this.$emit('query',newQuery)
      })
    }
    
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~stylus/variable"

  .search-box
    display: flex
    align-items: center
    box-sizing: border-box
    width: 100%
    padding: 0 6px
    height: 40px
    background: $color-highlight-background
    border-radius: 6px
    .icon-search
      font-size: 24px
      color: $color-background
    .box
      flex: 1
      margin: 0 5px
      height :2rem
      line-height: 1.1rem
      background: $color-highlight-background
      color: $color-text
      font-size: 1.1rem
      &::placeholder
        color: $color-text-d
    .icon-dismiss
      font-size: 16px
      color:gray
</style>


