<template>
  <transition name="list-fade">
    <div class="playlist"   v-show="isShowList" >
      <div class="list-wrapper"  >
        <div class="list-header">
          <h1 class="title">
           <i class='icon' :class='iconMode' @click='changeMode'></i>
            <span class="text">{{currentMode}}</span>
            <span class="clear" @click='deleteAll'  ><i class="icon-clear"></i></span>
          </h1>
        </div>
        <scroll ref="listContent"   class="list-content"  >
          <ul >
            <li  :key='it.id'   @click='selectItem(it,index)'  ref="listItem" class="item" v-for="(it,index) in sequenceList"  >
              <i  class="current" :class='currentSongIcon(it)' ></i>
              <span   class="text" @click='clickItem(index)'> {{it.name}} </span>
              <span  class="like" @click.stop='toggleFavorite(it)'>
                <i  class='icon' :class='iconFav(it)'  ></i>
              </span>
              <span   class="delete" @click.stop='deleteThisSong(it)'>
                <i class="icon-delete"></i>
              </span>
            </li>
          </ul>
        </scroll>
        <div class="list-operate">
          <div  class="add" @click.stop='showAdd'>
            <i class="icon-add"></i>
            <span class="text"  >添加歌曲到队列</span>
          </div>
        </div>
        <div   class="list-close" @click.stop='hideList'>
          <span>关闭</span>
        </div>
       </div>
      <add-song ref='add_song'  />
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
 import {mapActions,mapMutations}  from 'vuex'
  import scroll from '../commonComp/btscroll'
  import {playMode} from '../../common/js/config'
  import { MessageBox } from 'mint-ui';
  import {playerMixin} from '../../common/js/mixin'
  import addSong from '../add-song/add-song'
  export default {
    mixins:[playerMixin],
    components:{
      scroll,
      addSong
    },
    props:{
      isShowList:Boolean,
      default:false
    },
     updated(){
       this.$refs.listContent.refresh()
        this.scrollToCurrent(this.currentSong)
      this.$refs.listContent.refresh()
      
     },
     watch:{
      currentSong(newSong,oldSong){
         if(!this.isShowList || newSong.id===oldSong.id){
           return 
         }
          this.scrollToCurrent(newSong)
          setTimeout(()=>{
              this.$refs.listContent.refresh()
          }, 20)
         
      },
       
     },
     computed:{
      currentMode(){
        return this.mode === playMode.sequence?'顺序播放':this.mode === playMode.loop?'单曲循环':'随机播放'
      }
      
     },
     
     methods:{
        
       showAdd(){
        this.$refs.add_song.show()
         
       },
       hideAdd(){
        this.$refs.add_song.hide()
       },
       hideList(){
         this.$emit('hidePlayList')
       },
       currentSongIcon(it){
         if(this.currentSong.name === it.name){
           return 'icon-play'
         }
       },
       selectItem(it,index){
         if(this.mode===playMode.random){
           index=this.playList.findIndex(song=>{
             return song.id===it.id
           })
         }
          this.setPlaying(true)
         this.setCurrentIndex(index)
          this.$refs.listContent.refresh()
       },
       clickItem(index){
            
           this.$refs.listContent.scrollToElement(this.$refs.listItem[index],400)
            this.$refs.listContent.refresh()
       },
       scrollToCurrent(current){
        let  index= this.sequenceList.findIndex((item)=>{
           return current.id == item.id
         })
         console.log(index)
    
         this.$refs.listContent.scrollToElement(this.$refs.listItem[index-2],400)
         
       },
       deleteThisSong(it){
        this.deleteSong(it)

       },
       deleteAll(){
         MessageBox.confirm('确认清空当前播放列表?').then(() => {
       this.deleteListAll()
          }).catch(()=>{
              return
          });
       },
       
         ...mapActions(['deleteSong','deleteListAll'])
     },
      
      
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~stylus/variable"
  @import "~stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 100
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: $color-highlight-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon-loop
             width 30px
             font-size :1.5rem
             align-items :center
          .icon-sequence
             width 30px
             font-size :1.5rem
             align-items :center 
          .icon-random 
             width 30px
             font-size :1.5rem
             align-items :center
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: 1.2rem
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.3s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>


